body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
} 
a {
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{
  --buttonBackColour: red;
  --buttonFontColour: black;
  /*Diiferent Sizes*/
  --space05rem: 0.5rem;
  --space1rem: 1rem;
  --space105rem: 1.5rem;
  --space2rem: 2rem;
  --space205rem: 2.5rem;
  --space3rem: 3rem;
  --space305rem: 2.5rem;
  
  /*Heading */
  --headpadding: 2rem;
  --companyNameSize: 3rem;
}














.main {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.footer-main {
    background-color: dodgerblue;
    text-align: center;
    padding: var(--space1rem);
}
.head-main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: dodgerblue;
    padding: var(--space1rem) var(--space3rem);
}
.head-main > .company-name{
    font-size: var(--companyNameSize);
}
.registration-back {
    display: flex;
    flex-direction: column;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
    border: 2px solid black;
}
.registration-back > * {
    margin: 1rem auto;
    
}
.login-back {
    display: flex;
    flex-direction: column;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
}
.login-back > * {
    margin: 1rem auto;
    
}

.blogscard-main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 2px solid black;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-items: center;
    min-width: 900px;
    padding: var(--space05rem);
}
.zone-one > a {
    text-decoration: underline;
}

.zone-one,.zone-two {
    display: flex;
    flex-direction: column;
}
.blog-content {
    max-height: 50px;
    overflow: hidden;
}
.zone-two > span  {
    padding: var(--space05rem);
    transition: all 0.2s; 
    background-color: white;
    color: dodgerblue;
    font-weight: bold;
    border-radius: 50%;
}
.zone-two > span:hover {
    background-color: dodgerblue;
    color: white;
    cursor: pointer;
}
.dashboard-back {
    padding: 1rem;
}
.dashboard {
    margin: auto;
}

@media screen and (min-width: 650px) {
    ul > li {
        font-size: 2rem;
    }    
}
.home-main {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.user-card-back {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 1rem;
    grid-gap: 0.5rem;
}
.dashboard {
    margin: auto;
}
@media screen and (max-width: 600px) {
    .user-card-back {
        display: grid;
        grid-template-rows: auto auto;
        
    }
}
